<template>
    <div class="navbar">
        
        <div class="burger" @click="openMenu">
            <div class="burger-line " :class="{ 'burger-line-one': menuOpened }"></div>
            <div class="burger-line " :class="{'burger-line-two': menuOpened}"></div>
            <div class="burger-line " :class="{'burger-line-three': menuOpened}"></div>
            
        </div>
        <div class="menu" :class="{'menu-in': menuOpened}">
            <ul class="menu-list">
                <router-link tag="li" to="/" @click.native="openMenu">Main</router-link>                
                <router-link tag="li" to="albums" @click.native="openMenu">Albums</router-link>
                <router-link tag="li" to="about" @click.native="openMenu">About</router-link>
                <router-link tag="li" to="gallery" @click.native="openMenu">Gallery</router-link>
                
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            menuOpened: false
        }
    },
    methods: {
        openMenu() {
            this.menuOpened = !this.menuOpened;
        }
    }
}
</script>

<style scoped>

    /* navbar style and animation */

    .navbar {
        display: grid;       
        height: 90px;
        align-items: center;
        width: 100%;
        position: fixed;
        z-index: 999;
    }

    

    .burger {
        margin-right: 1.5rem;
        cursor: pointer;
        justify-self: flex-end;
        z-index: 999;
    }

    .burger-line {
    height: 3px;
    width: 25px;
    background: #FFB400;
    margin-bottom: 5px;
    transition: all 0.4s ease-out;
    }

    .burger-line-one {
        transform: rotate(45deg) translate(5px, 5px);
    }

    .burger-line-two {
        opacity: 0;
    }

    .burger-line-three {
        transform: rotate(-45deg) translate(7px, -6px);
    }

    /* menu style and animation */

    .menu {
        width: 100%;
        height: 100%;
        position: fixed;
        display: grid;
        justify-content: center;
        align-content: center;
        transform: translate(100%);
        transition: all 0.4s ease-out;
        background-color: rgba(255, 255, 255, .9);
        
    }  

    .menu-in {
        transform: translate(0);
        
    }

    .menu-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    }

    .menu-list li {
        color: #000;
        font-size: 2rem;
        cursor: pointer;
        transition: color 0.3s ease-in;
        padding: .5rem;
        margin: 0 .5rem;
        
    }

    .menu-list li:hover {
        color: #FFB400;
        transition: color 0.3s ease-in;
        
    }

    @media (min-width: 800px) {
        .menu-list li {
            font-size: 2.5rem;
        }
    }

</style>
